import { type MouseEventHandler, type RefObject } from 'react'

import CheckIcon from '@mui/icons-material/Check'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import { Box, Button, Card, Divider, Grid, Typography, useMediaQuery, useTheme } from '@mui/material'

interface IntroductionsProps {
  adminBenefitRef: RefObject<HTMLDivElement>
  userBenefitRef: RefObject<HTMLDivElement>
}

const Introductions: React.FC<IntroductionsProps> = ({ adminBenefitRef, userBenefitRef }) => {
  const NUM_ADVANTAGES = 4

  const theme = useTheme()
  const isMqUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))

  const adminAdvantages = [
    `회원 연락처, 등급,${isMqUpperThanSm ? ' ' : '\n'}활동 정보를 관리하세요.`,
    `멋진 홍보글을 작성하고${isMqUpperThanSm ? ' ' : '\n'}회원을 모집하세요.`,
    `각 행사마다 출석부와${isMqUpperThanSm ? ' ' : '\n'}회의록을 저장하세요.`,
    `회비 내역을 기록하고${isMqUpperThanSm ? ' ' : '\n'}결산안에 이용하세요.`
  ]
  const userAdvantages = [
    `관심있는 동아리를${isMqUpperThanSm ? ' ' : '\n'}검색할 수 있어요.`,
    `간편하게 동아리에${isMqUpperThanSm ? ' ' : '\n'}가입할 수 있어요.`,
    `출석, 회비, 등급 현황을${isMqUpperThanSm ? ' ' : '\n'}볼 수 있어요.`
  ]

  const handleAdvantageCardClick: (permission: 'admin' | 'user') => MouseEventHandler<HTMLDivElement> =
    permission => () => {
      if (permission === 'admin') {
        adminBenefitRef.current?.scrollIntoView({ behavior: 'smooth' })
      }

      if (permission === 'user') {
        userBenefitRef.current?.scrollIntoView({ behavior: 'smooth' })
      }
    }

  return (
    <Box p={2} paddingTop={5} sx={{ backgroundColor: theme.colors.primary.lighter }}>
      <Typography variant={isMqUpperThanSm ? 'h1' : 'h2'} textAlign="center">
        동아리의 모든 것<br />
        KLUB에서{isMqUpperThanSm ? ' ' : <br />}쉽고 간편하게
      </Typography>
      <Grid container display="flex" justifyContent="center" textAlign="center" mt={2}>
        <AdvantageCard
          type={'운영진'}
          advantages={adminAdvantages}
          maxAdvantageLength={NUM_ADVANTAGES}
          isMqMatched={isMqUpperThanSm}
          handleClick={handleAdvantageCardClick('admin')}
        />
        <AdvantageCard
          type={'동아리원'}
          advantages={userAdvantages}
          maxAdvantageLength={NUM_ADVANTAGES}
          isMqMatched={isMqUpperThanSm}
          handleClick={handleAdvantageCardClick('user')}
        />
      </Grid>
      <Box mt={2} textAlign="center">
        <ExpandMoreIcon fontSize="large" color="secondary" />
      </Box>
    </Box>
  )
}

export default Introductions

interface AdvantageCardProps {
  type: '운영진' | '동아리원'
  advantages: string[]
  maxAdvantageLength: number
  handleClick: React.MouseEventHandler<HTMLDivElement>
  isMqMatched: boolean
}

const AdvantageCard: React.FC<AdvantageCardProps> = ({
  type,
  advantages,
  maxAdvantageLength,
  handleClick,
  isMqMatched
}) => {
  return (
    <Grid item xs={12} sm={10} md={6} lg={5} xl={4}>
      <Card
        sx={{
          m: 2,
          p: 2,
          ':hover': {
            backgroundColor: '#eeeeee'
          },
          cursor: 'pointer'
        }}
        onClick={handleClick}
      >
        <Typography variant="h3" gutterBottom>
          {type}
        </Typography>
        <Divider sx={{ marginX: 3, marginY: 1 }} />
        <Box>
          {Array.from({ length: maxAdvantageLength }, (_, index) => (
            <Typography
              variant={isMqMatched ? 'h4' : 'h5'}
              key={index}
              mb={{ xs: 1, sm: 0.5 }}
              sx={{
                visibility: advantages[index] ? 'inherit' : 'hidden',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                wordBreak: 'keep-all',
                whiteSpace: 'pre'
              }}
            >
              <CheckIcon color="primary" sx={{ m: 1 }} />
              {advantages[index]}
            </Typography>
          ))}
        </Box>
        <Button variant="outlined">더 알아보기</Button>
      </Card>
    </Grid>
  )
}
